body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#fafafa;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body {
    font-family: Helvetica, sans-serif;
  }
  
  .react-autosuggest__container {
    position: relative;
  }
  
  .react-autosuggest__input {
    width: 240px;
    height: 15px;
    padding: 10px 20px;
    font-family: Helvetica, sans-serif;
    font-weight: 300;
    font-size: 16px;
    border: 1px solid #aaa;
    border-radius: 4px;
  }
  
  .react-autosuggest__input--focused {
    outline: none;
  }
  
  .react-autosuggest__input--open {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }
  
  .react-autosuggest__suggestions-container {
    display: none;
  }
  
  .react-autosuggest__suggestions-container--open {
    display: block;
    position: absolute;
    top: 51px;
    width: 280px;
    border: 1px solid #aaa;
    background-color: #fff;
    font-family: Helvetica, sans-serif;
    font-weight: 300;
    font-size: 16px;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    z-index: 2;
  }
  
  .react-autosuggest__suggestions-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }
  
  .react-autosuggest__suggestion {
    cursor: pointer;
    padding: 10px 20px;
  }
  
  .react-autosuggest__suggestion--highlighted {
    background-color: #ddd;
  }
  
.selection-kpis-container {
  position:fixed;
  height:100%;
  right:0;
  top:0;
  overflow-y:scroll;
  overflow-x:wrap;
  width:200px;
  padding:20px;
  background-color:rgba(249,249,249,0.5);
  font-size:12px;
  /*pointer-events:none;*/
}

.selection-kpis-container .municipality-name {
  font-weight:300;
  font-size:2em;
  margin: 1em 0;
}

.selection-kpis-container .kpi-title {
  margin: 0.75em 0 1.5em;
}

.selection-kpis-container .kpi-value-container {
  position:relative;
  height:30px;
}

.selection-kpis-container .kpi-value-container .kpi-value-bar {
  position:absolute;
  height:100%;
  height:10px;
  transition: width 0.5s, background-color 0.5s;
}

.selection-kpis-container .kpi-min {
  position:absolute;
  top:10px;
  left:0;
}

.selection-kpis-container .kpi-median {
  position:absolute;
  top:10px;
  margin-left:-25px;
  width:50px;
  text-align:center;
}

.selection-kpis-container .kpi-value {
  position:absolute;
  top:-15px;
}

.selection-kpis-container .kpi-max {
  position:absolute;
  top:10px;
  right:0;
}

.home-wrap .controls-container {
  position:fixed;
  top:0;
  left:0;
  opacity:0.8;
  width:200px;
  padding:1em;
}

.home-wrap .control {
  margin:0 0 1em;
}

.react-autosuggest__input {
  width:100%;
  box-sizing:border-box;
  height: 37px;
}

.home-wrap .viz-canvas {
  margin-top:50px;
}
.legends-container {
  position:fixed;
  top:0;
  left:230px;
  padding:1em;
  background-color:rgba(249,249,249,0.8);
  display:-webkit-flex;
  display:flex;
  pointer-events:none;
}

.legends-container .legend-title {
  font-size:12px;
}

.legends-container .legend-color {
  height:50px;
}

.legends-container .colors-blocks {
  position:relative;
  /*top: 2.1em;*/
  margin-top:0.5em;
  width:200px;
}

.legends-container .colors-blocks .block {
  position:absolute;
  top:0;
  height:10px;
}

.legends-container .colors-values {
  position:relative;
  width:200px;
}

.legends-container .colors-value {
  top:1em;
  position:absolute;
  font-size:12px;
}

.legends-container .colors-min {
  left:0;
}

.legends-container .colors-max {
  right:0;
  text-align:right;
}

.legends-container .legend-size {
  position:relative;
  margin-left:1em;
}

.legends-container .sizes-values {
  position:absolute;
  font-size:12px;
  left:0;
  height:40px;
  width:40px;
}

.legends-container .sizes-min {
  position:absolute;
  left:50%;
  width:40px;
  margin-left:-20px;
  bottom:3px;
  text-align:center;
}

.legends-container .sizes-max {
  position:absolute;
  text-align:center;
  left:50%;
  width:40px;
  margin-left:-19px;
  top:0;
}

.legends-container .sizes-circles {
  position:absolute;
  font-size:12px;
  margin-top:2px;
  left:0;
  height:40px;
  width:40px;
}


.legends-container .big-circle,
.legends-container .small-circle,
.legends-container .medium-circle {
  position:absolute;
  bottom:0;
  left:0;
  border:1px solid #333;
  background-color:#fff;
}

.legends-container .big-circle {
  left:0;
  height:40px;
  width:40px;
  border-radius:40px;
}

.legends-container .medium-circle {
  left:7px;
  height:25px;
  width:25px;
  border-radius:25px;
}

.legends-container .small-circle {
  left:17px;
  height:5px;
  width:5px;
  border-radius:5px;
}


